html {
  box-sizing: border-box;
  font-size: 10px; }   // 62.5%


*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  font-family: $font-family; }

body.lock {
	overflow: hidden; }

.container {
	max-width: #{$maxWidthContainer}px;
	margin: 0 auto;
	padding: 0 10px; }


main {
	overflow: hidden;
	min-height: 100% {
  flex: 1 0 auto; } }

footer {
  flex: 0 0 auto; }


input[type='number'] {
  -moz-appearance: textfield; }


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

a {
  color: inherit;
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

input,
textarea {
  border: none; }

textarea {
  resize: none; }

input::placeholder,
textarea::placeholder {
	color: inherit;
	font-size: 16px;
	font-weight: inherit;
	font-family: inherit; }

input[type="number"] {
  -moz-appearance: textfield; }


input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }


button {
  background: transparent;
  border: none;
  color: inherit;
  cursor: pointer; }

li {
  list-style: none; }

