@import 'import';


body {
  position: relative;
  &::before {
    content: '';
    display: none;
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.6);
    z-index: 50; }
  &::after {
    content: "";
    display: none;
    position: fixed;
    background: url(../img/loading.gif) no-repeat;
    background-size: cover;
    top: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
    transform: translate(-50%);
    z-index: 51; } }
body.--sending {
  overflow: hidden;
  &::before,
  &::after {
    display: block; } }




.popup {
  position: fixed;
  display: block;
  bottom: 0;
  right: 0;
  padding: 20px 40px;
  text-align: center;
  z-index: 60;
  color: $white;
  font: $text-medium;
  animation: fade-in 1s ease 1 forwards 0s;
  &_add {
    background: green; }
  &_remove {
    background: $blue; } }

.arrow-up {
  position: fixed;
  bottom: 0;
  right: 0;
  background: $blue;
  width: 60px;
  height: 60px;
  z-index: 5;
  cursor: pointer;
  transform: translate(0, 100%);
  opacity: 0;
  transition: transform 0.1s ease 0s, opacity 0.5s ease 0s;
  &.--visible {
    transform: translate(0);
    opacity: 1; }
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    background: url(../img/svg/sprite.svg#arrow-white) no-repeat;
    width: 11px;
    height: 18px;
    transform: translate(-50%); } }




// header =================================================================================
.header {
  position: relative;
  min-height: 250px;
  padding: 40px 0 0 0;
  background: $dark;

  &-top {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    margin: 0 0 50px 0;
    z-index: 8;
    transition: background 0.7s ease 0s, position 0.7s ease 0s, padding 0.7s ease 0s;
    &.--fixed {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      padding: 20px 0;
      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $dark;
        z-index: 3; } }

    &__inner {
      display: flex;
      justify-content: space-between; }

    &__logo {
      width: 78px; }

    &__burger {
      display: none;
      position: relative;
      background: transparent;
      width: 30px;
      height: 20px;
      z-index: 3;
      &:before,
      &:after,
      & span {
        position: absolute;
        width: 100%;
        height: 2px;
        background: #fff;
        left: 0; }

      & span {
        top: 9px;
        transition: transform 0.5s ease 0s; }
      &:before,
      &:after {
        content: ''; }
      &:before {
        top: 0;
        transition: transform 0.5s ease 0s, top 0.3s ease 0s; }
      &::after {
        bottom: 0;
        transition: transform 0.5s ease 0s, bottom 0.3s ease 0s; }
      &.--active {
        & span {
          transform: scale(0); }
        &::before {
          top: 9px;
          transform: rotate(45deg); }
        &::after {
          bottom: 9px;
          transform: rotate(-45deg); } } }

    &__contacts {
      & a {
        display: block;
        margin: 0 0 5px 0;
        color: $white;
        font: $text;
        transition: color 0.3s ease 0s; }
      & span {
        color: $white;
        font-size: 18px; } } }

  &__title {
    position: relative;
    margin: 0;
    color: $white;
    font: $h2;
    z-index: 1; }

  &__bg-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: 0; } }

.menu {
  align-self: center;

  &__list {
    display: flex;
    padding: 0;
    margin: 0; }

  &__item {
    color: $white;
    font-size: 18px;
    overflow: hidden;
    &:not(:last-child) {
      margin: 0 70px 0 0; }
    &::after {
      content: "";
      display: inline-block;
      width: 100%;
      height: 1px;
      background: $white;
      transform: translate(-105%, -12px);
      transition: transform 0.4s ease 0s; } } }

// products ====================================================================================================
.products {
  padding: 40px 0 50px;
  &-filter {
    margin: 0 0 30px 0;
    &__subtitle {
      margin: 0 50px 0 0;
      font-size: 18px;
      font-weight: 700; }
    &-details {
      display: inline-block;
      position: relative;
      padding: 10px 0px;
      font-size: 18px;
      user-select: none;
      cursor: pointer;

      &:not(:last-child) {
        margin-right: 55px; }
      &__name {
        display: block;
        position: relative;
        padding: 0 30px 0 0;
        font-size: 18px;
        &::after {
          content: "";
          position: absolute;
          background-image: url(../img/svg/sprite.svg#arrow);
          background-repeat: no-repeat;
          bottom: 0px;
          right: 5px;
          width: 15px;
          height: 15px; } }

      &__list {
        position: absolute;
        width: 170px;
        padding: 0;
        margin: 0;
        bottom: 0;
        z-index: 1;
        transform: translate(0, 65px); }

      &__item {
        padding: 10px 10px 5px 5px;
        background: #e9e9e9;

        cursor: pointer;
        &:not(:last-child) {
          border-bottom: 1px dashed $dark; }
        &.products-filter-details__item--active {
          background: $blue; }
        & button {
          display: block;
          margin: 0;
          padding: 0;
          font-size: 16px; } } } }


  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -10px; }

  &__card {
    position: relative;
    flex: 0 0 31%;
    margin: 0 10px 25px;
    visibility: visible; }
  &__btn-more {
    display: block;
    padding: 20px 115px;
    margin: 0 auto;
    background: transparent;
    border: 2px solid #CACACA;
    border-radius: 10px;
    transition: transform 0.3s ease 0s, background 0.5s ease 0s;
    &:active {
      transform: scale(0.9) translate(0, 10px); } } }

.card {
  position: relative;
  background: #F3F3F3;
  display: flex;
  flex-direction: column;
  border-radius: 0 0 10px 10px;
  overflow: hidden;

  &-top {
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 100%;
    top: 30px;
    left: 0;
    padding: 0 30px;
    &-sales {
      max-width: 80%;
      display: flex;
      align-items: flex-start;
      flex-wrap: wrap;
      row-gap: 5px;
      &__item {
        display: inline-block;
        padding: 10px 18px;
        margin: 0 5px 0 0;
        font-size: 16px;
        color: $white;
        border-radius: 10px; } }
    &__heart {
      align-self: flex-start;
      padding: 0;
      background: transparent;
      cursor: pointer;
      & svg {
        width: 45px;
        max-height: 40px;
        fill: gray;
        transition: fill 0.3s ease 0s; }
      &.--active svg {
        fill: #fff; }
      &:focus-visible {
        outline: 1px solid $white; } } }

  &__img {
    width: 100%;
    height: 260px;
    margin: 0 0 30px;
    & img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }

  &__inner {
    padding: 0 30px;
    margin: 0 0 30px;
    flex: 1 0 auto; }

  &__title {
    margin: 0 0 20px 0;
    font: $text; }

  &-params {
    display: flex;
    justify-content: space-between;
    margin: 0 0 20px;
    &::before {
      content: "";
      display: inline-block;
      width: 4px;
      min-height: 100%;
      background: $blue; }
    &__item {
      max-width: 80px;
      margin-left: 5px;
      font-size: 12px;
      & strong {
        display: block;
        font-size: 16px; } } }
  &__price {
    font: $text; }
  &__btn {
    width: 100%;
    padding: 20px 0;
    @include btn;
    text-align: center;
    font-weight: 700;
    color: $white;
    border-radius: 0 0 10px 10px;
    transition: transform 0.3s ease 0s, background 0.3s ease 0s; } }



// offer =========================================================================================
.offer {
  background: $dark;
  padding: 80px 0 90px;
  &__inner {
    padding: 0 80px;
    display: flex;
    justify-content: space-between; }
  &-text {
    &__title {
      margin: 0 0 20px 0;
      color: $white;
      font: $h1; }

    &__subtitle {
      display: block;
      max-width: 300px;
      color: $white;
      font-size: 28px; } }

  &__result {
    padding: 20px 30px;
    background: $blue;
    color: $white; }

  &__result,
  &__status-error {
    display: inline-block;
    margin: 0 0 10px;
    font-size: 14px;
    white-space: nowrap; }

  &__status-error {
    color: $red; } }

.form {
  display: flex;
  &__inner {
    display: flex;
    flex-direction: column;
    margin: 0 20px 0 0; }
  &__email {
    width: 300px;
    padding: 24px 30px;
    border-radius: 10px;
    &::placeholder {
      font-size: 14px; }
    &:focus-visible {
      outline: none;
      box-shadow: 0 0 0 2px $blue; }
    &.--error {
      border: 2px solid $red; } }
  &__label {
    margin: 20px 0 0;
    color: $white;
    font-size: 14px;
    @include checkbox-custom; }
  &__btn {
    align-self: flex-start;
    padding: 24px 75px;
    @include btn;
    color: $white;
    font-weight: 700;
    border-radius: 10px; } }


@media screen and (min-width: 1024px) {
  .menu__item:hover:after {
    transform: translate(0%, -12px); }
  .header-top__contacts a:hover {
    color: $blue; }
  .products__btn-more:hover {
    background: $blue; } }

@media screen and (max-width: 1200px) {
  .offer__inner {
    padding: 0 30px; }
  .offer-text__title {
    font-size: 36px; }
  .offer-text__subtitle {
    font-size: 24px; } }


@media screen and (max-width: 1024px) {
  .menu__item:not(:last-child) {
    margin: 0 30px 0 0; }

  .menu__item::after {
    display: none; }

  .header {
    min-height: 220px; }

  .header__title {
    font-size: 32px; }

  .products__card {
    flex: 0 0 47%; }

  .offer__inner {
    padding: 0;
    justify-content: space-evenly; }

  .form__btn {
    padding: 20px 40px; }

  .form__email {
    width: 230px;
    padding: 20px 10px; }

  .offer-text__title {
    font-size: 32px; }

  .offer-text__subtitle {
    max-width: 220px;
    font-size: 20px; } }

@media screen and (max-width: 767px) {
  .header {
    padding: 125px 0 0; }

  .header-top {
    position: absolute;
    top: 30px; }

  .header-top__inner {
    align-items: center; }

  .header-top__burger {
    display: block;
    order: 1; }

  .header-top__logo {
    width: 50px;
    z-index: 3; }

  .header-top__contacts a {
    position: relative;
    margin: 0;
    font-size: 18px;
    z-index: 3; }

  .header-top__contacts span {
    display: none; }

  .menu {
    position: absolute;
    background: $blue;
    top: -40px;
    left: 50%;
    width: 100vw;
    height: 110vh;
    transform: translate(-50%, -105%);
    transition: transform 0.7s ease 0s;
    will-change: transform;
    z-index: 2;
    &.--active {
      transform: translate(-50%, -2%); } }

  .menu__list {
    flex-direction: column;
    padding: 150px 0 0 0; }

  .menu__item {
    font-size: 24px;
    margin: 0 auto 20px; }

  .menu__item:not(:last-child) {
    margin: 0 auto 20px; }

  .products-filter {
    margin: 0 0 10px 0; }

  .products__card {
    margin: 0 5px 20px; }

  .card__inner {
    padding: 0 10px;
    margin: 0 0 20px; }

  .card__img {
    height: 210px;
    margin: 0 0 20px; }
  .card-params {
    margin: 0 0 10px; }

  .card__title,
  .card__price {
    font-size: 20px; }

  .card-top {
    padding: 0 15px; }

  .products__btn-more {
    padding: 20px 40px; }

  .offer {
    padding: 60px 0; }

  .offer__inner {
    flex-direction: column;
    justify-content: center; }

  .offer-text {
    margin: 0 0 20px 0; }

  .offer-text__title {
    margin: 0 0 10px 0; }

  .offer-text__subtitle {
    max-width: none; } }

@media screen and (max-width: 550px) {
  .header {
    min-height: 150px;
    padding: 80px 0 0 0; }

  .header-top__logo {
    display: none; }

  .header__title {
    font-size: 28px; }

  .products-filter__subtitle,
  .products-filter-details:not(:last-child) {
    margin: 0 15px 0 0; }

  .products-filter__subtitle,
  .products-filter-details__name {
    font-size: 14px; }

  .products-filter-details__list {
    width: 120px; }

  .products-filter-details__item button {
    font-size: 12px; }

  .products-filter-details__list {
    transform: translate(-30px, 50px); }

  .card-params::before {
    display: none; }

  .card-top-sales__item {
    padding: 6px 10px; }

  .card-params {
    flex-wrap: wrap;
    flex-direction: column;
    row-gap: 10px; }

  .card-params__item {
    max-width: none;
    margin-left: 0;
    &-age,
    &-paws {
      display: flex;
      flex-direction: row-reverse;
      align-items: flex-end;
      margin-right: auto;
      & strong {
        margin-left: 5px;
        white-space: nowrap;
        font-size: 14px; } } }

  .card-top {
    top: 10px;
    padding: 0 10px; }

  .card-top-sales__item {
    font-size: 12px; }

  .card__title,
  .card__price {
    font-size: 16px; }

  .card__btn {
    padding: 15px 0; }

  .card__img,
  .card__title,
  .card__inner {
    margin: 0 0 10px; }

  .card__img {
    height: 170px; }

  .card-top__heart svg {
    width: 30px;
    height: 28px; }

  .products__btn-more {
    font-size: 14px; }

  .offer-text__title {
    font-size: 26px; }

  .offer-text__subtitle {
    font-size: 14px; }



  .form {
    flex-wrap: wrap;
    row-gap: 20px;
    // justify-content: center
 }    // text-align: center

  .form__inner {
    margin: 0 10px 0 0; }

  .form__label {
    margin: 10px 0 0; }

  .form__email {
    padding: 15px 10px; }

  .form__btn {
    padding: 15px 30px;
    font-size: 14px; } }

@media screen and (max-width: 360px) {
  .header__title {
    font-size: 26px; }

  .products__card {
    flex: 0 0 280px; }

  .products-filter-details__name {
    padding: 0 20px 0 0;
    font-size: 12px;
    &::after {
      width: 10px;
      height: 10px; } }

  .offer-text__subtitle {
    max-width: 200px; } }
