@mixin font($font_name, $file_name, $weight, $style) {
  @font-face {
    font-family: $font_name;
    font-display: swap;
    src: url('../fonts/#{$file_name}.woff2') format("woff2"), url('../fonts/#{$file_name}.woff') format("woff");
    font-weight: $weight;
    font-style: $style; } }


.custom-switcher {
  width: 80px;
  height: 40px;
  padding: 0 7px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  border: 1px solid var(--color-white);
  border-radius: 90px;
  cursor: pointer;
  & input {
    position: absolute;
    top: 0;
    left: 0;
    width: 1px;
    height: 1;
    opacity: 0;
    &:checked {
      & ~span {
        background: #EFD81D;
        transform: translateX(-36px); } } }
  & span {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    background: #969696;
    border-radius: 50%;
    transition: all 0.5s ease 0s;
    & svg {
      max-width: 20px;
      max-height: 20px; } } }

@mixin btn {
  background: $blue;
  transition: background 0.35s ease 0s, transform 0.35s ease 0s;
  &:active {
    transform: scale(0.9); }
  &:hover {
    background: lighten(#6EBBD3, 7%); }
  &:disabled {
    background: $dark;
    cursor: default; }
  &:focus-visible {
    outline: none;
    box-shadow: inset 0 0 0 2px $dark; } }

@mixin checkbox-custom() {
  position: relative;
  display: inline-block;
  padding-left: 30px;
  min-height: 18px;
  & span {
    position: absolute;
    width: 18px;
    height: 18px;
    top: -1px;
    left: 0;
    background: transparent;
    border: 1px solid $white;
    border-radius: 5px;
    transition: background 0.5s ease 0s;
    &::after {
      content: "";
      position: absolute;
      left: 6px;
      top: 2px;
      width: 5px;
      height: 9px;
      border: solid $white;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
      opacity: 0;
      transition: opacity .5s; } }
  & input {
    opacity: 0;
    position: absolute;
    width: 1px;
    height: 1px;
    &:checked ~span {
      background: $blue;
      border-color: $blue;
      &::after {
        opacity: 1; } }
    &:focus-visible ~span {
      box-shadow: 0 0 5px 2px $white; } } }

@keyframes fade-in {
  0% {
    transform: translate(0, 105%); }
  100% {
    transform: translate(0); } }
